import React from 'react'
import './Work.css'
import Slider from "react-slick";
import WorkCard from './WorkCard'
import { HideUntilLoaded } from 'react-animation'

var settings = {
    dots: false,
    lazyLoad: true,
    infinite: true,
    speed: 1500,
    slidesToShow: 1,
    slidesToScroll: 1,
    initialSlide: 4,
    responsive: [
        {
            breakpoint: 1024,
            settings: {
                slidesToShow: 1,
                slidesToScroll: 1,
                infinite: true,

            }
        },
        {
            breakpoint: 600,
            settings: {
                slidesToShow: 1,
                slidesToScroll: 1
            }
        },
        {
            breakpoint: 480,
            settings: {
                slidesToShow: 1,
                slidesToScroll: 1
            }
        }
    ]
}

let images = [
    "https://i.imgur.com/xjk6rug.png",
    "https://i.imgur.com/SfDQj54.png",
    "https://i.imgur.com/tGyyPpk.png",
    "https://i.imgur.com/SoA4Klu.png",
    "https://i.imgur.com/kpskGDs.png",
    "https://i.imgur.com/DJDPOSA.png",
    "https://i.imgur.com/8uuxBaS.png",
    "https://i.imgur.com/XJOkxFT.png",
    "https://i.imgur.com/f7XZNph.png"

]

function Work() {
    document.title = "My Works";

    return (
        <div className="works">
            <Slider  {...settings}>


                {
                    images.map((image) => (
                        <HideUntilLoaded
                            animationIn="fadeIn"
                            imageToLoad={images}
                            Spinner={() => <div>Loading...</div>}
                        >
                            <div className="work__card_list">
                                <WorkCard image={image} />
                            </div>
                        </HideUntilLoaded>
                    ))
                }

            </Slider>
        </div>

    )
}

export default Work
